        "hongbaoContent": "### Q1: 怎么获得红包？ * 抢微信朋友分享的红包获得； * 玩游戏领红包（仅限Android客户端）； ### Q2: 红包如何使用？ * 红包仅限订单选择在线支付时使用； * 每个订单限使用一个红包，每天最多可使用2个红包； * 每个红包只能使用一次，不能叠加或拆分使用； * 红包如有使用金额门槛，则购物车金额需满足金额门槛才可使用； * 红包如有品类、商家、手机号或时段限制，订单需满足对应条件后才可使用； * 红包使用不设找零，如红包可抵扣金额大于订单金额，则订单需支付0.01元。 ### Q3: 下单的时候使用了红包，但是后来订单取消了，红包还会返还吗？ * 会的，订单无效后红包会自动返还到您的账户里。 ### Q4: 红包兑换码怎样兑换成红包，怎样查看红包？ * 在个人中心 > 我的红包 > 兑换红包，输入兑换码进行兑换。 ### Q5: 预订早餐、预订午餐的红包怎么使用？在哪里查询？ * 预订早餐、预订午餐的红包暂时不支持直接点外卖使用，并且当前点外卖的红包也暂时不支持在预订早餐、预订午餐时使用。 * 预订早餐、预订午餐的红包从APP首页点击“预订早餐”按钮进入后在右上角的个人中心中查找预订早餐、预订午餐的红包。 ### Q6: 互斥红包怎么使用？ * 互斥红包不与其他优惠活动（包含但不限于：新用户专享、满X元减X元、满X元赠XX）同时使用。 ### Q7: 红包领取规则是什么? * 同一个手机号一天限领取三个。 * 同一注册用户一天限领取三个。 * 注册用户红包直接发送至用户账户里。 * 同一个手机号一天最多使用两个红包。 * 未收到红包短信的用户注意查看手机里的拦截短信。 * 有任何问题请拨打客服电话：10105757。 ### Q8：首单红包的使用条件？ * 首单红包需同时满足以下条件才可用，且首单红包不与其他优惠（首减、满减、满赠、套餐赠、折扣菜）同享。 * 新用户（设备、手机号、饿了么账号、支付账号均未在饿了么外卖下过单）首次下单。 * 仅限在线支付使用，且收餐人手机号、领取红包时输入的手机号和账号中的手机号需为同一手机号。 * 微信小程序中“商超便利”和“果蔬生鲜”分类暂未开通。 ", 
        "hongbaoCaption": "红包问题", 
        "freshmanContent": "* 新用户是根据设备、饿了么账号、下单手机号进行判断，如果您下单时以上信息均为首次使用则符合饿了么定义的新用户 * 仅限新用户使用移动端app提交订单并选择在线支付时享受（饿配送订单选择餐到付款也可享受） * 在本设备上曾经有过购买记录行为的手机不能参加本活动 * 新用户首单立减金额各区域不同 * 活动仅支持手机客户端最新版本", 
        "freshmanCaption": "活动细则", 
        "forgotContent": "### 如何补签？ 如忘记签到，可通过下单补签，漏签当日至红包领取日前一天每1笔有效订单可补签 1 天（仅限在线支付订单），例：如您在活动第 3 天忘记签到，只要你在第 3 天至第 9 天内完成1笔有效的订单，即可完成补签。", 
        "forgotCaption": "补签规则", 
        "discountContent": "### Q1: 我是新用户，为什么不能享受新用户下单立减优惠／新用户首单红包？ * 新用户是指您的下单设备、手机号、饿了么账号、支付账号都是第一次在饿了么平台使用。以上任意条件不满足，都不能享受新用户优惠。 * 如果监测到您不满足以上的信用条件，您的订单将被系统拦截或无偿取消。 * 新用户立减优惠、新用户红包、满减特价菜活动不能同时使用。在您首次下单过程中，我们会优先为您选择优惠最大（优惠金额最高）的。 ### Q2: 首单被取消，再次下单还能享受新用户立减优惠／新用户红包吗？ * 您主动取消或商家、骑手取消的订单，饿了么会返还您的新用户资格，这时您仍然可以享受新用户的优惠。 * 您被取消的首单因为「使用的支付账户曾下过单」，必须更换新的支付账户才可以享受新用户优惠。 * 但您取消的原因是账户异常等原因，则不能享受新用户优惠。 ### Q3: 我第一次在 \"A\" 商家下单，\"A\" 商家有参加「新用户」活动，我为什么享受不到新用户的立减优惠？ * 活动中的新用户指的是饿了么网站的新用户，而不是指 \"A\" 商家的新客户。如果您不符合饿了么网站新用户的定义则无法享受于新用户相关的活动优惠。 ### Q4: 一个红包能拆开多次使用吗？ * 不能，一个红包只能一次性使用，不能分开使用。 ### Q5: 下单的时候使用了红包，但是后来订单取消了，红包还会返还吗？ * 会的，订单无效后红包会自动返还到您的账户里。 ### Q6: 红包兑换码怎样兑换成红包，怎样查看红包？ * 在个人中心 > 我的红包 > 兑换红包，输入兑换码进行兑换。 ### Q7: 在线支付订单取消后，钱怎么返还？ * 使用饿了么账户余额支付，订单无效后，所有款项将退到您的饿了么账户；使用第三方支付订单无效后，支付款项将于2个工作日内返还到您的第三方支付账户，红包退还到饿了么账户。 ### Q8: 每天限制优惠3单，前面下了一个在线支付的单子，由于未付款，订单自动取消了，这单会计算我的参与活动次数吗？ * 不会。未付款的订单不会计入您使用优惠的次数。 ### Q9: 每天红包使用次数有限制吗？ * 同一个手机号每天最多能使用两个红包优惠（不含预订早餐和预订午餐），使用红包的订单出现退单情况不计入限制。 ", 
        "discountCaption": "优惠说明", 
        "disclaimerContent": "# Disclaimer: The application developed by Ele.me or have. Ele.me makes it clear that, your business activities through the participation of the software have nothing to do with Apple Inc., and Apple does not assume any responsibilities of the program. Mobile phone brand partners from Ele.me, such as Apple and App Store. Sponsors of non-mobile phone clients from Ele.me, nor do you get the mobile phone client’s intellectual property rights or any other rights. If the mobile phone client violates the third party’s intellectual property rights, Ele.me will bears the corresponding legal responsibility. Ele.me has the right but not the obligation to improve or correct any errors or defects. Ele.me will not represent or warrant the accuracy or reliability of any contents,information or advertisements which users obtain from the application. Moreover, Ele.me will not be responsible for any products or information which users purchase by the mobile client. Users shall use the application at their own risk. # 免责声明： 本应用由饿了么开发或拥有。饿了么在此申明，用户通过本软件参加的商业活动，与Apple Inc.无关，Apple不承担该程序的任何责任。 饿了么手机客户端的品牌合作商，如Apple，AppStore，非饿了么手机客户端的赞助商，也未拥有该客户端的知识产权或其他任何权益。因饿了么手机客户端侵犯第三方知识产权的，由饿了么承担相应的法律责任。 饿了么有权但无义务，改善或更正本手机客户端的任何错误或缺陷。 用户通过饿了么手机客户端及其中的任何链接、下载获得的一切信息、内容或广告，饿了么不声明或保证其正确性或可靠性；并且对于用户经本手机客户端上的展示、内容、广告而购买、取得的任何产品、或信息，饿了么不负保证责任。用户自行承担使用本软件的风险。", 
        "disclaimerCaption": "免责声明", 
        "couponContent": "### Q1: 什么是商家代金券？ 商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。 ### Q2: 怎么获得商家代金券？ * 进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。 * 「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。 ### Q3: 商家代金券使用条件 * 商家代金券仅限在指定商家使用 * 商家代金券仅限在线支付使用 * 每个订单只能使用一张商家代金券，不可与其他代金券叠加使用", 
        "couponCaption": "代金券说明", 
        "businessContent": "### 品牌合作 <a href=\"mailto:marketing_coop@ele.me\">marketing_coop@ele.me</a> ### 媒体合作 <a href=\"mailto:pr@ele.me\">pr@ele.me</a>", 
        "businessCaption": "商务合作", 
        "balanceContent": "### Q1: 使用余额的条件 为了保护账户安全，使用余额前必须先绑定手机号。 ### Q2: 余额可以怎么用？ 余额可以在饿了么平台上提现，当余额大于等于待支付金额时可以在支持在线支付的商家中进行消费。提现功能将于2016年12月25日00:00全面开放。 ### Q3:我要如何提现？ 为了保护账户和资金安全，您在提现前需要输入真实姓名和用该姓名开通的银行卡号、选择开户行，并验证已绑定饿了么账号的手机号。每日只能提现1次，每次限额50元。若提现金额超过50元，点击【提现】时系统会提示您已超额，请您修改提现金额。 ### Q4:为什么会提现失败？ 可能原因有：您的姓名、开户行、银行卡号等信息不匹配；您当日的提现次数和金额超过限制；您的账户存在异常，被风控拦截。 ", 
        "balanceCaption": "余额问题", 
        "awesomeRestaurantContent": "# 超赞商家", 
        "awesomeRestaurantCaption": "超赞商家", 
        "anonymousBuyContent": "![anonymous buy](https://fuss10.elemecdn.com/f/08/bb0bcfc2a10e26524633e38ea47f6jpeg.jpeg)", 
        "anonymousBuyCaption": "匿名购买", 
        "activityContent": "### Q1: 我是新用户，为什么不能享受新用户下单立减优惠／新用户首单红包？ * 新用户是指您的下单设备、手机号、饿了么账号、支付账号都是第一次在饿了么平台使用。以上任意条件不满足，都不能享受新用户优惠。 * 新用户立减优惠、新用户红包、满减特价菜活动不能同时使用。在您首次下单过程中，我们会优先为您选择优惠最大（优惠金额最高）的。 ### Q2: 商品活动规则 * 新用户下首单时，如果用户自己主动退单，则下次不可再享受新用户优惠，如果是商家或客服取消订单，则可以再次享受新用户优惠。 * 6.0版本（含）之前的所有版本，用户在下单时不再享受任何优惠（包含新用户、在线满减、商品折扣、红包等）。 * 移动端所有活动，相同手机号或设备每天只限3单，超过3单收取原价。 * 每个订单最多享受3份活动优惠，超过3份的其余商品收取原价。 * 下单手机号和所选择下单的商家不在同一城市，不享受优惠活动。 * 配送费不计入满减活动的门槛。 ### Q3: 我第一次在 \"A\" 商家下单，\"A\" 商家有参加「新用户」活动，我为什么享受不到新用户的立减优惠？ * 活动中的新用户指的是饿了么网站的新用户，而不是指 \"A\" 商家的新客户。如果您不符合饿了么网站新用户的定义则无法享受于新用户相关的活动优惠。 ", 
